//
import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Game = () => {
    const navigate = useNavigate();

    const handleEndGame = () => {
        navigate('/', { state: { dialogIndex: 67 } });
    };


    return (
        <div>
            <button onClick={handleEndGame}></button>
        </div>
    );
};

export default Game;
