import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import assemblyImage from '../../assets/DIY-01.png'; // 
import './Game2.scss';

const Game2 = () => {
  const [isAssembling, setIsAssembling] = useState(false); // 
  const navigate = useNavigate();

  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 9 } });
  };

  const handleStartAssembling = () => {
    setIsAssembling(true); // 
  };

  return (
    <div className="container2">
      {!isAssembling ? (
        <div className="content2">
          <button className="assembleButton2" onClick={handleStartAssembling}></button>
        </div>
      ) : (
        <div className="content2">
          <img src={assemblyImage} alt="" className="assemblyImage2" />
          <button className="finishButton2" onClick={handleEndGame}></button>
        </div>
      )}
    </div>
  );
};

export default Game2;
