import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import mapImageFront from '../../assets/.jpg'; // 
import mapImageBack from '../../assets/.jpg'; // 
import audio1 from '../../assets/.mp3'; // 1
import audio2 from '../../assets/.mp3'; // 2
import audio3 from '../../assets/.mp3'; // 3
import './Game4.scss'; //  SCSS 

const Game4 = () => {
  const [inputValue, setInputValue] = useState(''); // 
  const [showEndButton, setShowEndButton] = useState(false); // 
  const [isFront, setIsFront] = useState(true); // 
  const navigate = useNavigate();

  // 
  const handlePlayAudio = (index) => {
    const audioFiles = [audio1, audio2, audio3]; // 
    const audio = new Audio(audioFiles[index]);
    audio.play();

    // 
    if (index === 1) {
      checkInputValue();
    }
  };

  // 
  const checkInputValue = () => {
    if (inputValue === '') {
      setShowEndButton(true); // 
    }
  };

  // 
  const handleEndGame = () => {
    navigate('/', { state: { dialogIndex: 38 } });
  };

  // 
  const clearResultAndAudio = () => {
    setInputValue(''); // 
    setShowEndButton(false); // 
  };

  // 
  const toggleMap = () => {
    setIsFront(!isFront); // 
  };

  return (
    <div className="container">
      <div className="mapContainer">
        {/*  */}
        <img src={isFront ? mapImageFront : mapImageBack} alt="" className="mapImage" />
      </div>
      <div className="toggleButtonContainer">
        {/*  */}
        <button className="map-button" onClick={toggleMap}>
          
        </button>
      </div>
      <div className="buttonsContainer">
        {/*  */}
        <button
          className="audio-button"
          onClick={() => handlePlayAudio(0)}
        >
           1
        </button>
        <button
          className="audio-button"
          onClick={() => handlePlayAudio(1)}
        >
           2
        </button>
        <button
          className="audio-button"
          onClick={() => handlePlayAudio(2)}
        >
           3
        </button>
      </div>
      <div className="resultContainer">
        {/*  */}
        <input
          type="text"
          value={inputValue}
          onChange={(e) => setInputValue(e.target.value)}
          onBlur={checkInputValue} // 
          placeholder=""
          className="input"
        />
        {showEndButton && (
          <div>
            {/*  */}
            <button className="map-button" onClick={handleEndGame}>
              
            </button>
          </div>
        )}
      </div>
    </div>
  );
};

export default Game4;
