import React, { useState, useEffect } from 'react';
import { Button } from 'antd';
import { useLocation } from 'react-router-dom';
import Dialog from '../Dialog/index';

import found from '../../assets/picture/youfoundit.jpg';
import Npc from '../../assets/1-.png';

import kids1 from '../../assets/picture/ming/-/1-.png';
import kids2 from '../../assets/picture/ming/-/2-.png';
import kids3 from '../../assets/picture/ming/-/3-.png';
import kids4 from '../../assets/picture/ming/-/3-2.png';
import kids5 from '../../assets/picture/ming/-/4-.png';
import kids6 from '../../assets/picture/ming/-/5-.png';
import kids7 from '../../assets/picture/ming/-/6-.png';
import kids8 from '../../assets/picture/ming/-/7-.png';

import grandpa1 from '../../assets/picture/grandpa/1-.png';
import grandpa2 from '../../assets/picture/grandpa/2-.png';
import grandpa3 from '../../assets/picture/grandpa/3-.png';
import grandpa4 from '../../assets/picture/grandpa/4-.png';
import grandpa5 from '../../assets/picture/grandpa/5-.png';
import grandpa6 from '../../assets/picture/grandpa/6-.png';

import frog1 from '../../assets/picture/Frog/1-.png';
import frog2 from '../../assets/picture/Frog/2-.png';
import frog3 from '../../assets/picture/Frog/3-.png';

const Login = () => {
  const [dialogVisible, setDialogVisible] = useState(false);
  const [currentDialogIndex, setCurrentDialogIndex] = useState(0);
  const [imageVisible, setImageVisible] = useState(false);

  const location = useLocation();

  const savePageState = () => {
    const pageState = {
      currentDialogIndex,
      imageVisible,
    };
    sessionStorage.setItem('loginPageState', JSON.stringify(pageState));
  };

  useEffect(() => {
    const savedState = JSON.parse(sessionStorage.getItem('loginPageState'));
    if (savedState) {
      setCurrentDialogIndex(savedState.currentDialogIndex);
      setImageVisible(savedState.imageVisible);
    }
    return () => {
      sessionStorage.removeItem('loginPageState');
    };
  }, []);

  useEffect(() => {
    if (location.state?.dialogIndex !== undefined) {
      setCurrentDialogIndex(location.state.dialogIndex);
      setDialogVisible(true);
    }
  }, [location.state]);


  // 
  const dialogs = [
    { title: '', content: '' },
    { title: '', content: '', imageUrl: Npc },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: ' ', imageUrl: Npc },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa2 },
    { title: '', content: '', imageUrl: kids3 },
    { title: '', content: '' },
    { title: '', content: '...' },
    { title: '', content: '!.........?!', imageUrl: kids5 },
    { title: '', content: '?...!!', imageUrl: grandpa3 },
    { title: '', content: '~', imageUrl: grandpa4 },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '?', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: 'YA!!!', imageUrl: kids6 },
    //
    { title: '', content: '' },
    { title: '', content: '?!', imageUrl: kids7 },
    { title: '', content: '', imageUrl: grandpa5 },
    //
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: grandpa6 },
    { title: '', content: '', imageUrl: kids8 },
    //
    { title: '', content: '.........()' },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '...', imageUrl: grandpa1 },
    { title: '', content: '.........() ' },
    { title: '', content: '...?', imageUrl: kids5 },
    { title: '', content: '' },
    { title: '', content: '! ', imageUrl: grandpa4 },
    { title: '', content: '??', imageUrl: kids2 },
    { title: '', content: '...?', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '...' },
    //
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '~', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    //-
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: kids1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids6 },
    //
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '~...', imageUrl: grandpa6 },
    { title: '', content: '...', imageUrl: grandpa6 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: kids4 },
    { title: '', content: '?', imageUrl: grandpa3 },
    { title: '', content: '', imageUrl: kids6 },
    { title: '', content: '', imageUrl: grandpa1 },
    { title: '', content: '' },
    { title: '', content: '' },
    //
    { title: '', content: '', imageUrl: kids6 },
    { title: '', content: '', imageUrl: frog1 },
    { title: '', content: '!', imageUrl: kids1 },
    { title: '', content: '', imageUrl: frog2 },
    { title: '', content: '', imageUrl: kids2 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '?', imageUrl: frog3 },
    { title: '', content: '?', imageUrl: kids6 },
    { title: '', content: '............', imageUrl: grandpa6 },
    { title: '', content: '......', imageUrl: grandpa6 },
    { title: '', content: '...', imageUrl: kids4 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: kids5 },
    { title: '', content: '!', imageUrl: grandpa4 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '' },
    { title: '', content: '', imageUrl: grandpa4 },
    { title: '', content: '!', imageUrl: kids4 },
    { title: '', content: '', imageUrl: kids4 },
    { title: '', content: '', imageUrl: frog3 },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    { title: '', content: '' },
    //?###
  ];

  const openDialog = (index = currentDialogIndex) => {
    setDialogVisible(true);
    setImageVisible(true);
    savePageState();
  };

  const closeDialog = () => {
    setDialogVisible(false);
    setCurrentDialogIndex(0);
    setImageVisible(false);
    savePageState();
  };

  const handlePreviousDialog = () => {
    setCurrentDialogIndex((prevIndex) => {
      if (prevIndex > 0) {
        const newIndex = prevIndex - 1;
        savePageState();
        return newIndex;
      }
      return prevIndex;
    });
  };

  const handleNextDialog = () => {
    setCurrentDialogIndex((prevIndex) => {
      if (prevIndex < dialogs.length - 1) {
        const newIndex = prevIndex + 1;
        savePageState();
        return newIndex;
      }
      return prevIndex;
    });
  };

  return (
    <div className='dialog'>
      {dialogVisible && (
        <Dialog
          visible={dialogVisible}
          onClose={closeDialog}
          dialogData={dialogs}
          currentIndex={currentDialogIndex}
          onPrevious={handlePreviousDialog}
          onNext={handleNextDialog}
          setCurrentDialogIndex={setCurrentDialogIndex}
          dialogs={dialogs}
          savePageState={savePageState}
        />
      )}
      {!dialogVisible && (
        <div className="centered-container">
          <img
            src={found}
            alt="Placeholder Image"
            className="centered-image"
          />
          <Button type="primary" className="foundButton" onClick={openDialog}>
            
          </Button>
        </div>
      )}
      {imageVisible && (
        <div className="people-container">
          {/* Display corresponding image */}
        </div>
      )}
    </div>
  );
};

export default Login;